import React from 'react';
import Header from '../../components/Header/Header';
import Sidebar from '../../components/SideBar/Sidebar';
import { Button, Card } from 'react-vant';
import { useParams, useNavigate } from 'react-router-dom';
import { ArrowLeft } from '@react-vant/icons';
import { petRecords } from '../../mock/petData';
import * as echarts from 'echarts';
import './petdetail.css';

interface PetInfo {
  id: string;
  name: string;
  breed: string;
  age: string;
  weight: string;
  gender: string;
  avatar: string;
}

// 健康趋势监测图表组件
const HealthTrendChart: React.FC = () => {
  const chartRef = React.useRef<HTMLDivElement>(null);
  const chartInstance = React.useRef<echarts.ECharts>();

  React.useEffect(() => {
    if (!chartRef.current) return;

    // 初始化图表
    chartInstance.current = echarts.init(chartRef.current);

    const option = {
      title: {
        text: '健康趋势监测',
        left: 'left',
        textStyle: {
          color: '#333',
          fontSize: 18,
        },
        padding: [0, 0, 0, 20],
      },
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          type: 'shadow',
        },
      },
      legend: {
        data: ['体重变化 (kg)', '每日食量 (g)'],
        top: '5%',
        right: '10%',
        textStyle: {
          fontSize: 14,
        },
      },
      xAxis: {
        type: 'category',
        data: ['6月', '7月', '8月', '9月', '10月', '11月', '12月'],
        axisLabel: {
          color: '#666',
        },
        axisLine: {
          lineStyle: {
            color: '#ccc',
          },
        },
      },
      yAxis: [
        {
          type: 'value',
          name: '体重(kg)',
          position: 'left',
          min: 4.5,
          max: 5.2,
          interval: 0.1,
          axisLabel: {
            color: '#666',
          },
          splitLine: {
            lineStyle: {
              color: '#eee',
            },
          },
        },
        {
          type: 'value',
          name: '食量(g)',
          position: 'right',
          min: 60,
          max: 76,
          interval: 2,
          axisLabel: {
            color: '#666',
          },
          splitLine: {
            show: false,
          },
          axisLine: {
            lineStyle: {
              color: '#ccc',
            },
          },
        },
      ],
      series: [
        {
          name: '体重变化 (kg)',
          type: 'line',
          yAxisIndex: 0,
          smooth: true,
          lineStyle: {
            color: '#1890ff',
            width: 2,
          },
          areaStyle: {
            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
              { offset: 0, color: 'rgba(24, 144, 255, 0.3)' },
              { offset: 1, color: 'rgba(24, 144, 255, 0.1)' },
            ]),
          },
          symbol: 'circle',
          symbolSize: 8,
          itemStyle: {
            color: '#1890ff',
          },
          data: [4.5, 4.6, 4.6, 4.8, 5.0, 5.1, 5.2],
        },
        {
          name: '每日食量 (g)',
          type: 'line',
          yAxisIndex: 1,
          smooth: true,
          lineStyle: {
            color: '#52c41a',
            width: 2,
            type: 'dashed',
          },
          symbol: 'circle',
          symbolSize: 8,
          itemStyle: {
            color: '#52c41a',
          },
          data: [60, 66, 66, 70, 75, 68, 65],
        },
      ],
      grid: {
        left: '10%',
        right: '10%',
        bottom: '15%',
        containLabel: true,
      },
    };

    chartInstance.current.setOption(option);

    return () => {
      if (chartInstance.current) {
        chartInstance.current.dispose();
      }
    };
  }, []);

  return (
    <div className="health-trend-chart">
      <div ref={chartRef} style={{ width: '100%', height: '400px' }} />
      <div className="chart-note">
        <span>ℹ️ 行为观察：11月活动量有所下降，可能与气温降低有关，建议增加室内互动玩具。</span>
      </div>
    </div>
  );
};

function petdetail() {
  return (
    <div style={{ display: 'flex', flexDirection: 'column', height: '100vh' }}>
      <Header />
      <Sidebar />
      
      <div className="Pet-Basic-Information"></div>
      {/* 添加 echarts中的报表一个 */}
      <div className="Health-Overview">
        <HealthTrendChart />
      </div>
      <div></div>
      <div></div>
      <div></div>
    </div>
  );
}

export default petdetail;